import 'ol/ol.css';
import DragAndDrop from 'ol/interaction/DragAndDrop';
import Draw from 'ol/interaction/Draw';
import GeoJSON from 'ol/format/GeoJSON';
import GeometryType from 'ol/geom/GeometryType';
import Map from 'ol/Map';
import Modify from 'ol/interaction/Modify';
import Snap from 'ol/interaction/Snap';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import View from 'ol/View';
import {toStringHDMS} from 'ol/coordinate';
import {fromLonLat, toLonLat} from 'ol/proj';
import sync from 'ol-hashed';
import {Fill, Stroke, Style, Text} from 'ol/style';
import Overlay from 'ol/Overlay';

//! [imports]
import {getArea} from 'ol/sphere';
import colormap from 'colormap';
//! [imports]

//! [color]
const min = 1e8; // the smallest area
const max = 2e13; // the biggest area
const steps = 50;
const ramp = colormap({
  colormap: 'blackbody',
  nshades: steps
});

function clamp(value, low, high) {
  return Math.max(low, Math.min(value, high));
}

function getColor(feature) {
  if (feature.get('covid_deaths') == 0) {
      return "#a9cf54";
  } else if (feature.get('covid_deaths') > 0 && feature.get('covid_deaths') <= 10) {
      return "#fafa6e";
  } else if (feature.get('covid_deaths') > 10 && feature.get('covid_deaths') <= 20) {
      return "#fbef60";
  } else if (feature.get('covid_deaths') > 20 && feature.get('covid_deaths') <= 50) {
      return "#fce552";
  } else if (feature.get('covid_deaths') > 50 && feature.get('covid_deaths') <= 80) {
      return "#fcda45";
  } else if (feature.get('covid_deaths') > 80 && feature.get('covid_deaths') <= 130) {
      return "#fdce38";
  } else if (feature.get('covid_deaths') > 130 && feature.get('covid_deaths') <= 180) {
      return "#fec32b";
  } else if (feature.get('covid_deaths') > 180 && feature.get('covid_deaths') <= 250) {
      return "#feb71e";
  } else if (feature.get('covid_deaths') > 250 && feature.get('covid_deaths') <= 320) {
    return "#feac10";
  } else if (feature.get('covid_deaths') > 320 && feature.get('covid_deaths') <= 410) {
    return "#fe9f00";
  } else if (feature.get('covid_deaths') > 410 && feature.get('covid_deaths') <= 500) {
    return "#fe9300";
  } else if (feature.get('covid_deaths') > 500 && feature.get('covid_deaths') <= 610) {
    return "#fe8600";
  } else if (feature.get('covid_deaths') > 720 && feature.get('covid_deaths') <= 850) {
    return "#fd7800";
  } else if (feature.get('covid_deaths') > 850 && feature.get('covid_deaths') <= 980) {
    return "#fc6900";
  } else if (feature.get('covid_deaths') > 980 && feature.get('covid_deaths') <= 1500) {
    return "#fb5a00";
  } else if (feature.get('covid_deaths') > 1500 && feature.get('covid_deaths') <= 2000) {
    return "#f94805";
  } else if (feature.get('covid_deaths') > 2000 && feature.get('covid_deaths') <= 2500) {
    return "#f7320f";
  } else if (feature.get('covid_deaths') > 2500) {
      return "#f40916";
  } else {
      return "grey";
  }
}
//! [color]

function getCountry(feature) {
  name =  feature.get('name');
  return name;
}

const map = new Map({
  target: 'map-container',
  view: new View({
    center: [0, 0],
    zoom: 2
  })
});

sync(map);

const source = new VectorSource();

//! [style]
const layer = new VectorLayer({
  source: source,
  style: function(feature) {
    return new Style({
      fill: new Fill({
        color: getColor(feature)
      }),
      text: new Text({
        text: getCountry(feature),
        font: '12px Calibri,sans-serif',
        fill: new Fill({
          color: '#000'
        }),
      }),
      stroke: new Stroke({
        color: 'rgba(255,255,255,0.8)'
      })
    });
  }
});
//! [style]


map.addLayer(layer);

map.addInteraction(new DragAndDrop({
  source: source,
  formatConstructors: [GeoJSON]
}));


//! [popup]
var element = document.getElementById('popup');

var popup = new Overlay({
  element: element,
  positioning: 'bottom-center',
  stopEvent: false
});
map.addOverlay(popup);

map.on('click', function(evt) {

  var feature = map.forEachFeatureAtPixel(evt.pixel,
  function(feature, layer) {
    return feature;
  });
  if (feature) {
    console.log(feature)
    var covid_deaths = feature.get('covid_deaths');
    var covid_cases = feature.get('covid_cases');

    var element = popup.getElement();
    var coordinate = evt.coordinate;
    var hdms = toStringHDMS(toLonLat(coordinate));

    $(element).popover('destroy');
    popup.setPosition(coordinate);
    $(element).popover({
      placement: 'top',
      animation: false,
      html: true,
      content: '<p>Cases</p><code>' + covid_cases + '</code> </br> <p>Deaths</p><code>' + covid_deaths + '</code>'
    });
    $(element).popover('show');
  }
});
//! [popup]

const format = new GeoJSON({featureProjection: 'EPSG:3857'});
